<template>
  <div id="box">
    <div class="content">
        <div class="section">
            <!--<div class="item">
                <label>
                    行业<span>*</span>
                </label>
                <span>
                    <Combobox :init='listInit' :type="Type">
                        <div slot="box" class="combox" @click.stop="rt">
                            <div class="pup" @click.stop="rt">请选择行业</div>
                            <ul class="listul">
                                <li :class="changeValue==item.value?'active':''" v-for="(item,i) in list" :key="i" :data-key="listInit.value==item.value?0:1" :data-index="i" :data-value="item.value" @click.stop="changFn($event,item)">{{item.text}}</li>
                            </ul>
                            <div class="queding" @click.stop="rt">
                                <button @click.stop="oklist">确定</button>
                            </div>
                        </div>
                    </Combobox>
                </span>
            </div>-->
            <div class="item" v-if="Type==2">
                <label for="date">
                    发布时间<span>*</span>
                </label>
                <span>
                    <input disabled="true" type="text" name="date" id="date" v-model="date"/>
                </span>
            </div>
            <div class="item">
                <label for="title">
                    标题<span>*</span>
                </label>
                <span>
                    <input type="text" v-validate="'required'" name="title" id="title" placeholder="请输入标题" v-model="title"/>
                    <i class="tip" v-show="errors.has('title')">{{errors.first('title')}}</i>
                </span>
            </div>
            <div class="item">
                <label for="content">
                    内容<span>*</span>
                </label>
                <span>
                    <textarea v-validate="'required'" name="content"  id="content" placeholder="请输入内容" cols="30" rows="4" v-model="content"></textarea>
                    <i class="tip" v-show="errors.has('content')">{{errors.first('content')}}</i>
                </span>
            </div>
        </div>
        <div class="section">
            <div class="item submintItem">
                <button v-if="Type==1" class="button" @click="ok">确定</button>
                <button v-if="Type==2" class="button" @click="ok">保存</button>
            </div>
        </div>
    </div>
  </div>
</template>
<script>
    import { Validator } from 'vee-validate';
    import com from '../assets/common.js';
    import Exotic from '../config.js';
    export default{
        created(){
            let that=this;
            const dictionary = {
                zh_CN: {
                    messages: {
                        email: () => '邮箱格式不正确哦',
                        required:(field)=>field+"不能为空"
                    },
                    attributes:{
                        title:'标题',
                        content:'内容'
                    }
                }
            };
            Validator.localize(dictionary);
            if(this.$route.query.token!=undefined){
                this.token=this.$route.query.token;
            }
            if(this.$route.query.id!=undefined){
                this.id=this.$route.query.id;
            }
            if(this.$route.query.type!=undefined){
                this.Type=this.$route.query.type;
            }
            //查询行业的ajax请求
            Exotic.ajaxData(that.token,Exotic.urlMap.other.industryList, {}, 'get', function(data){
               console.log(data.list);
               for(let item of data.list){
                   that.list.push({
                       value:item.id,
                       text:item.name
                   })
               }
               that.listInit.value=that.list[0].value;
               that.changeValue=that.list[0].value;
               that.listInit.text=that.list[0].text;
            });
            if(this.Type==2){
                var param = {storePostingsId: that.id};
                Exotic.ajaxData(that.token,Exotic.urlMap.other.topic, param, 'get', function(data){
                    that.title=data.storePostings.title; //标题
                    var createDate = com.getTimes(data.storePostings.createDate)
                    that.date=createDate; //发布时间
                    that.content=data.storePostings.content; //内容
                    console.log(data);
                });
            }
        },
        data(){
            return {
                token:null,
                Type:1,//类型
                id:'',//ID
                title:'',//标题
                date:'',//发布时间
                //行业列表数据
                list:[],
                content:'',//内容
                changeValue:1,//选中的行业
                //行业列表默认
                listInit:{
                    value:null,//默认选中的行业
                    id:'hy',//ID
                    key:false, //弹出层控制
                    text:''//默认选中的行业文字
                }
            }
        },
        methods:{
            //弹出防止冒泡
            rt(){
                return true;
            },
            //运算找出对应ID的数据
            stext(arr,obj){
                for(let i=0;i<arr.length;i++){
                    if(arr[i].value==obj){
                        return arr[i];
                    }
                }
            },
            //行业变更
            changFn(ev,res){
                this.changeValue=res.value;
            },
            //确认行业
            oklist(){
                let text=this.stext(this.list,this.changeValue).text;
                let value=this.stext(this.list,this.changeValue).value;
                this.listInit.text=text;
                this.listInit.value=value;
                this.listInit.key=false;
            },
            ok(){
                let that=this;
               if (that.title == '') {
                    Exotic.tip({txt:"请填写标题"});
                    return false;
                } else if (that.content == '') {
                    Exotic.tip({txt:"请填写内容"});
                    return false;
                }else{
                    if(that.Type==1){
                        var param = {
                            token: that.token,
                            title: that.title,
                            content: that.content
                        }
                        that.MintUi.Indicator.open({
                            text: '加载中...',
                            spinnerType: 'fading-circle'
                        })
                        Exotic.ajaxData(that.token,Exotic.urlMap.other.add, param, 'post', function(data){
                            Exotic.tip({txt:"新建帖子成功"});
                            that.MintUi.Indicator.close()
                            var u = navigator.userAgent;
                            var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; //android终端
                            var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
                            if(isiOS){
                                //点击保存按钮的时候传递值返回列表
                                window.location.href = "#SuccessToPrevPage*arg=0";
                            }else if(isAndroid){
                                window.location.href = "fenzu://jscome/backToPrevPage?update=16";
                            }
                        }); 
                    }else if(that.Type==2){
                        var param = {
                            token: that.token,
                            storePostingsId:that.id,
                            title: that.title,
                            content: that.content
                        }
                        that.MintUi.Indicator.open({
                            text: '加载中...',
                            spinnerType: 'fading-circle'
                        })
                        Exotic.ajaxData(that.token,Exotic.urlMap.topic.update, param, 'post', function(data){
                            that.MintUi.Indicator.close();
                            Exotic.tip({txt:"修改帖子成功"});
                            var u = navigator.userAgent;
                            var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; //android终端
                            var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
                            if(isiOS){
                                //点击保存按钮的时候传递值返回列表
                                window.location.href = "#SuccessToPrevPage*arg=0";
                            }else if(isAndroid){
                                window.location.href = "fenzu://jscome/backToPrevPage?update=16";
                            }
                        }); 
                    }
                    
                }
                
            }
        },
        mounted(){
          
        }
    }
</script>
<style lang="scss">
@import '../assets/index.scss';
.combox{
    overflow:hidden;
    display: flex;
    flex-direction: column;
}
.combox .pup{
    flex:1;
}
.combox .queding{
    flex:1;
}
.queding button{
    margin: 1.2rem auto;
    padding: .8rem 0;
    width: 80%;
    display: block;
    text-align: center;
    background-color: #39C073;
    color: #fff;
    border-radius: .5rem;
    outline: none;
    font-size: 1.2rem;
    border: 0;
}
</style>




